@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap");

/* styles taken from Net Ninja on youtube*/
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}
.navbar h1 {
  color: #5983ff;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #5983ff;
}
.loading-screen {
  text-align: center;
  margin-top: 50px;
}
.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}
.all-sessions-main {
  max-width: 600px;
  margin: 10px auto;
  padding: 20px;
  border-bottom: 1px solid #fafafa;
}
.session-in-list {
  background: #5983ff;
  border-radius: 10px;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.2);
  width: 30%;
  padding: 10px;
  margin-bottom: 15px;
}
.session-in-list h2 {
  text-align: center;
  font-size: 20px;
  color: white;
  margin-bottom: 4px;
}
.session-in-list p {
  text-align: left;
  color: white;
  margin-bottom: 2px;
}
.single-session {
  max-width: 600px;
  text-align: center;
  margin: 30px auto;
  padding: 20px;
}
.single-session h2 {
  font-size: 60px;
  color: #5983ff;
  margin-bottom: 8px;
}
.single-session p {
  font-size: 25px;
  margin: 10px;
}
.single-session button {
  background: #5983ff;
  color: #fff;
  margin: 30px;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label {
  font-size: 20px;
  text-align: left;
  display: block;
}
.create h2 {
  margin-top: 25px;
  font-size: 25px;
  color: #5983ff;
  margin-bottom: 40px;
}
.create input {
  width: 50%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #5983ff;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
.not-found {
  margin-top: 50px;
  text-align: center;
}
